<template>
  <el-row class="center">
    <el-col class="left" :span="17">
      <div class="left-title">
        <div class="left-title-one">
          <div class="left-title-one-text1">输入总地址</div>
          <div class="left-title-one-text2">
            <el-input v-model="input" placeholder="将地址粘贴到此,例如：C:\Program Files\Java\jre1.8.0_201"></el-input>
          </div>
          <el-row class="left-title-one-text-btn">
            <el-button type="primary" plain @click="clickFun()">确定</el-button>
          </el-row>
        </div>
        <div class="left-title-two">
          <div class="left-title-one-text-tisp">（包含全部待测队伍项目的文件地址）</div>
        </div>
        <div class="left-title-testBtn">
          <el-row class="left-title-one-text-btn">
            <el-button type="primary" plain @click="getData()">开始测试</el-button>
            <el-button type="primary" plain @click="stopTest()">停止</el-button>
          </el-row>
        </div>
      </div>
      <div class="left-table">
        <el-table
          style="width:100%"
          :data="tableData"
          max-height="600"
          ref="teamTable"
          v-loading="!isgetData"
          element-loading-text="测试中"
          element-loading-background="rgba(f, f, f, 0.8)"
          :header-cell-style="{'font-size':'18px'}"
        >
          <el-table-column
            fixed="left"
            prop="teamNum"
            align="center"
            label="队伍编号"
            :render-header="renderHeader"
          ></el-table-column>
          <el-table-column
            align="center"
            label="智能合约"
            sortable
            sort-by="CCScore"
            :sort-orders="['ascending', 'descending']"
          >
            <el-table-column prop="CCScore" align="center" label="得分" width="150"></el-table-column>
            <el-table-column prop="CCPassingRate" align="center" label="通过率" width="150"></el-table-column>
          </el-table-column>
          <el-table-column
            align="center"
            label="应用层"
            sortable
            sort-by="apiScore"
            :sort-orders="['ascending', 'descending']"
          >
            <el-table-column prop="apiScore" align="center" label="得分"></el-table-column>
            <el-table-column prop="apiPassingRate" align="center" label="通过率"></el-table-column>
          </el-table-column>
          <el-table-column
            prop="total"
            align="center"
            label="项目总分"
            sortable
            :sort-orders="['ascending', 'descending']"
          ></el-table-column>
          <el-table-column align="center" label="详细">
            <template slot-scope="scope">
              <el-button @click.native.prevent="shownDetail(scope.row)" type="text" size="small">详细</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-col>
    <el-col class="right" :span="7">
      <div class="right-header">{{detailNum==0?'显示队伍的详细测试信息':'当前显示第'+detailNum+'队的测试信息'}}</div>
      <div class="right-center">
        <div class="right-center-item">
          <el-table
            :data="CCdetailData"
            max-height="400"
            :header-cell-style="{'height':'60px','font-size':'16px'}"
            :row-class-name="CCtableRowClassName"
            
          >
            <el-table-column align="center" label="智能合约">
              <template slot-scope="scope">{{CCdetailData[scope.$index][0]}}</template>
            </el-table-column>
          </el-table>
          <div class="right-center-item-foot">
            <div v-if="detailNum!=0">
              测试共检测{{CCdetailData.length}}项
              <br />
              检测通过{{detailData.CCpassNum}}项
              <br />
              得分为 {{detailData.CCScore}}
            </div>
          </div>
        </div>
        <div class="right-center-item">
          <el-table
            :data="apidetailData"
            max-height="400"
            :header-cell-style="{'height':'60px','font-size':'16px'}"
            :row-class-name="apitableRowClassName"
          >
            <el-table-column align="center" label="应用层">
              <template slot-scope="scope">{{apidetailData[scope.$index][0]}}</template>
            </el-table-column>
          </el-table>
          <div class="right-center-item-foot">
            <div v-if="detailNum!=0">
              测试共检测{{apidetailData.length}}项
              <br />
              检测通过{{detailData.apipassNum}}项
              <br />
              得分为 {{detailData.apiScore}}
            </div>
          </div>
        </div>
      </div>
      <div
        v-if="detailNum!=0"
        class="right-foot"
      >队伍{{detailNum}}，在“智能合约”与“应用层”两项共得分{{detailData.total}}</div>
      <div v-else class="right-foot"></div>
    </el-col>
  </el-row>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "",
  data() {
    return {
      timer: Object,
      isgetData: true,
      input: "",
      tableData: [],
      detailNum: 0,
      detailData: [],
      CCdetailData: [],
      apidetailData: []
    };
  },
  computed: {
    ...mapState([])
  },
  methods: {
    ...mapMutations([]),
    //自定义队伍编号列的表头
    renderHeader(h, { column }) {
      return h(
        "div",
        {
          style: "cursor:pointer;font-size:20px",
          on: {
            click: this.$refs.teamTable.clearSort //点击时恢复列表顺序
          }
        },
        "队伍编号"
      );
    },
    //查看队伍的数据详情
    shownDetail(detailData) {
      this.detailData = detailData;
      this.detailNum = detailData.teamNum;
      this.CCdetailData = [];
      this.apidetailData = [];
      for (var i = 0; i < detailData.CCDetails.length; i++) {
        this.CCdetailData.push(detailData.CCDetails[i].split(":"));
      }
      for (var i = 0; i < detailData.apiDetails.length; i++) {
        this.apidetailData.push(detailData.apiDetails[i].split(":"));
      }
    },
    /*
     * 智能合约数据样式
     * fail:不通过，返回样式为红色
     * pass:通过，返回样式为绿色
     */
    CCtableRowClassName({ rowIndex }) {
      if (this.CCdetailData[rowIndex][1].indexOf("fail") !== -1) {
        return "fail-row";
      } else if (this.CCdetailData[rowIndex][1].indexOf("pass") !== -1) {
        return "pass-row";
      }
      return "";
    },
    /*
     * 应用层数据样式
     * fail:不通过，返回样式为红色
     * pass:通过，返回样式为绿色
     */
    apitableRowClassName({ rowIndex }) {
      if (this.apidetailData[rowIndex][1].indexOf("fail") !== -1) {
        return "fail-row";
      } else if (this.apidetailData[rowIndex][1].indexOf("pass") !== -1) {
        return "pass-row";
      }
      return "";
    },
    preview(event) {
      let files = document.getElementById(this.id).files[0];
      this.imgDataUrl = this.getObjectURL(files);
      this.$emit("sendImgUrl", this.imgDataUrl, this.id);
    },
    getObjectURL(file) {
      let url = null;
      if (window.createObjectURL != undefined) {
        // basic7
        url = window.createObjectURL(file);
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      } else if (window.URL != undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file);
      }
      return url;
    },
    /*
     * 验证评分文件路径
     * code: 2:成功；3:没有此文件夹
     */
    clickFun(res) {
      // console.log("clickFun");
      if (this.input == "") {
        this.$message.error("请输入地址");
        return;
      }
      this.$axios
        .get("/invalidPath?path=" + this.input)
        .then(response => {
          if (response.data.code == 2) {
            if (response.data.data != null) {
              var str = "";
              for (var i = 0; i < response.data.data.length; i++) {
                str += response.data.data[i] + "<br/>";
              }
              this.$alert(str, "缺少路径", { dangerouslyUseHTMLString: true });
            } else {
              this.$message({
                message: "无缺少路径",
                type: "success"
              });
            }
          } else {
            this.$message.error(response.data.message);
          }
        })
        .catch(err => this.$message.error("项目文件夹不存在"));
    },
    stopTest() {
      clearInterval(this.timer);
      this.isgetData = true;
    },
    getData() {
      // console.log("goTest");
      if (this.input == "") {
        this.$message.error("请输入地址");
        return;
      }
      var _this = this;
      _this.isgetData = false;
      _this.timer = setInterval(function() {
        if (_this.isgetData != true) {
          _this.goTest();
        } else {
          clearInterval(_this.timer);
        }
      }, 1000);
    },
    /*获取所有队伍测试结果信息
     * code : 1,还在评分，需要做一个轮询; 2:成功; 3: 没有此文件路径，
     */
    goTest() {
      this.$axios
        .get("/scoring?path=" + this.input)
        .then(response => {
          if (response.data.code == 1) {
            // this.$message("正在评分，请稍等");
          } else if (response.data.code == 2) {
            this.tableData = response.data.data;
            for (var i = 0; i < this.tableData.length; i++) {
              this.tableData[i].apipassNum = Math.ceil(
                this.tableData[i].apiPassingRate *
                  this.tableData[i].apiDetails.length
              );
              this.tableData[i].CCpassNum = Math.ceil(
                this.tableData[i].CCPassingRate *
                  this.tableData[i].CCDetails.length
              );
              this.tableData[i].apiPassingRate =
                this.tableData[i].apiPassingRate.toFixed(4) * 100 + "%";
              this.tableData[i].CCPassingRate =
                this.tableData[i].CCPassingRate.toFixed(4) * 100 + "%";
              this.tableData[i].total =
                this.tableData[i].apiScore + this.tableData[i].CCScore;
              this.tableData[i].teamNum = i + 1;
              this.apidetailData = [];
              this.CCdetailData = [];
              this.isgetData = true;
              this.detailData = [];
              this.detailNum = 0;
            }
            this.$message({
              message: "测试完成",
              type: "success"
            });
          } else if (response.data.code == 3) {
            this.$message.error(response.data.message);
            this.isgetData = true;
          }
        })
        .catch(err => {
          this.$message.error("没有此文件路径");
          this.isgetData = true;
        });
    }
  },
  beforeCreate() {
    // console.log("创建前：");
    this.$axios
      .get("/scoring?path=./projects")
      .then(response => {
        // console.log(response);
        if (response.data.code == 2) {
          // console.log(response.data.data);
          // console.log(response.data.data.length);
          this.tableData = response.data.data;
          for (var i = 0; i < this.tableData.length; i++) {
            this.tableData[i].apipassNum = Math.ceil(
              this.tableData[i].apiPassingRate *
                this.tableData[i].apiDetails.length
            );
            this.tableData[i].CCpassNum = Math.ceil(
              this.tableData[i].CCPassingRate *
                this.tableData[i].CCDetails.length
            );
            this.tableData[i].apiPassingRate =
              this.tableData[i].apiPassingRate.toFixed(4) * 100 + "%";
            this.tableData[i].CCPassingRate =
              this.tableData[i].CCPassingRate.toFixed(4) * 100 + "%";
            this.tableData[i].total =
              this.tableData[i].apiScore + this.tableData[i].CCScore;
            this.tableData[i].teamNum = i + 1;
          }
        }
        // console.log("this.tableData");
        // console.log(this.tableData);
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.center {
  /* width: 2380px; */
  width: 100%;
  /*height: 1130px; */
  display: -webkit-flex; /* Safari */
  display: flex;
  flex-flow: center nowrap;
  justify-content: flex-start;
  align-content: flex-start;
  margin-bottom: 40px;
}
.left {
  /* width: 1180px; */
  /* height: 1130px; */
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-content: flex-start;
}
.left-title {
  /* width: 1180px; */
  width: 100%;
  height: 100px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-content: flex-start;
}
.left-table {
  width: 95%;
  /* width: 100%; */
  height: 600px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-content: flex-start;
  margin: 20px auto;
}

.left-title-one {
  /* width: 1178px; */
  width: 100%;
  height: 50px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
}
.left-title-two {
  /* width: 1178px; */
  width: 100%;
  height: 30px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
}
.left-title-testBtn {
  /* width: 1178px; */
  width: 100%;
  height: 50px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  margin: 10px 0 0 0;
}
.left-title-one-text1 {
  width: auto;
  height: auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  font-weight: 500;
  font-size: 24px;
  margin-left: 30px;
  font-family: "PingFang SC";
}
.left-title-one-text2 {
  width: auto;
  height: auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  font-weight: 500;
  font-size: 28px;
  margin-left: 10px;
  width: 500px;
}
.left-title-one-text-tisp {
  width: auto;
  height: auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  font-weight: 300;
  font-size: 16px;
  margin-left: 10px;
}
.left-title-one-text-btn {
  width: 100px;
  height: 40px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  margin-left: 30px;
}

.right {
  /* width: 1200px;
  height: 1130px; */
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-content: flex-start;
}
.right-header {
  justify-content: flex-start;
  align-content: flex-start;
  margin: 60px 0 0 0px;
  font-weight: 500;
  font-size: 23px;
}

.right-center {
  /* width: 1170px;
  height: 700px; */
  width: 96%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-content: flex-start;
  margin: 0 auto;
}
.right-center-item {
  width: 50%;
  height: 500px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-content: flex-start;
  margin: 30px 0 0 0px;
  border: #ebeef5 solid 1px;
  box-sizing: border-box;
}
.right-center-item-foot {
  width: 50%;
  /* height: 76px; */
  margin: 10px auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  white-space: nowrap;
  /* overflow: hidden; */
}
.right-foot {
  width: 96%;
  height: 100px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-content: center;
  border: 1px solid #ebeef5;
  border-top: none;
  box-sizing: border-box;
  margin: 0 auto;
  padding-top: 35px;
  vertical-align: middle;
}
.myred {
  background-color: red;
}
</style>
<style>

.el-table .fail-row {
  background: rgba(250, 4, 4, 0.068);
  color: #ff0000;
}

.el-table .pass-row {
  background: #a1ff6e2f;
  color: #009419;
}
.el-table .caret-wrapper .sort-caret{
  width: 0;
  height: 0;
  border: 8px solid transparent;
  border-top-color: transparent;
  position: absolute;
  left: 7px;
}
.el-table .caret-wrapper .sort-caret.ascending {
    border-bottom-color: #C0C4CC;
    top: -1px;
}
.el-table .caret-wrapper .sort-caret.descending {
    border-top-color: #C0C4CC;
    bottom: 0px;
}
.el-table .descending .sort-caret.descending {
    border-top-color: #409EFF;
}
.el-table .ascending .sort-caret.ascending {
    border-bottom-color: #409EFF;
}
</style>


